@import url('https://fonts.googleapis.com/css?family=Oswald:300,400|Roboto+Condensed|Roboto:300,400');

$fontRoboto: 'Roboto', sans-serif;
$fontRobotoCond: 'Roboto Condensed', sans-serif;
$fontOswald: 'Oswald', sans-serif;

@import './containers/App/App';
@import './containers/BookDisplay/BookDisplay';
@import './containers/Book/Book';
@import './containers/Modal/Modal';
@import './containers/Search/Search';
@import './components/Loading/Loading';


* {
  box-sizing: border-box;
}

html {
  background: radial-gradient(rgba(40, 44, 52, .95) 0%, rgba(40, 44, 52, 0.5) 90%), url('./media/booksBG.jpg');
  background-size: cover;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

#root {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
