@import url(https://fonts.googleapis.com/css?family=Oswald:300,400|Roboto+Condensed|Roboto:300,400);
.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative; }
  .App .modal-button {
    opacity: 1;
    border: none;
    position: fixed;
    bottom: 1em;
    right: 1em;
    font-size: 1em;
    font-weight: 100;
    letter-spacing: .04em;
    font-family: "Oswald", sans-serif;
    background: #2196F3;
    color: #fff;
    z-index: 99;
    line-height: 1;
    border-radius: 5px;
    padding: .5em 1em;
    box-shadow: 1px 1px 8px 2px #000000c7;
    cursor: pointer;
    -webkit-transition: all .5s ease;
    transition: all .5s ease; }
    .App .modal-button:hover {
      opacity: .95; }
    .App .modal-button i {
      font-weight: 100;
      opacity: .8; }

.BookDisplay {
  display: flex;
  flex-wrap: wrap;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  padding: 1em;
  align-items: center; }

.Book {
  width: 29%;
  display: flex;
  align-items: center;
  margin: 2%;
  height: 190px;
  background: #333d46eb;
  border-radius: 3px; }
  .Book .BookContent {
    text-decoration: none;
    display: flex;
    height: 100%;
    padding: 10px;
    width: 100%; }
    .Book .BookContent .BookImage {
      display: flex;
      padding-right: 10px; }
      @media screen and (max-width: 1080px) {
        .Book .BookContent .BookImage {
          width: 30%; } }
      .Book .BookContent .BookImage img {
        display: block; }
    .Book .BookContent .BookCopy {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      position: relative;
      flex-grow: 1;
      background: #2a3b44cc;
      padding-left: 10px;
      box-shadow: inset 0px 0px 2px 0px #0000007a; }
      .Book .BookContent .BookCopy button {
        position: absolute;
        top: -5px;
        right: -10px;
        background: none;
        border: none;
        cursor: pointer; }
        .Book .BookContent .BookCopy button i {
          font-size: 1.5em;
          color: #d2d200; }
      .Book .BookContent .BookCopy h5 {
        display: flex;
        flex-direction: column;
        text-align: left;
        color: #fff;
        margin: 0;
        font-size: 15px;
        font-weight: 100;
        font-family: "Roboto Condensed", sans-serif; }
        @media screen and (min-width: 1080px) {
          .Book .BookContent .BookCopy h5 {
            font-size: 18px; } }
        .Book .BookContent .BookCopy h5 span {
          font-style: italic;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          letter-spacing: .05em; }

.Modal {
  display: flex;
  width: 100%;
  height: 100%;
  z-index: 9999;
  top: 0px;
  left: 0px;
  position: fixed;
  background: rgba(8, 8, 8, 0.97);
  padding: 2%;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start; }
  .Modal h2 {
    font-family: "Oswald", sans-serif;
    font-size: 2em;
    color: #fff;
    font-weight: 100;
    margin: 0;
    text-transform: uppercase;
    padding-bottom: 10px;
    text-align: center;
    width: 100%; }
  .Modal .hideModalBtn {
    position: absolute;
    top: 0;
    right: 0;
    background: none;
    border: none;
    color: #fff;
    font-size: 2em;
    font-weight: 100;
    cursor: pointer;
    opacity: 1;
    -webkit-transition: all .3s ease;
    transition: all .3s ease; }
    .Modal .hideModalBtn:hover {
      opacity: .8; }

.Search {
  display: flex;
  flex-direction: column;
  width: 40%;
  align-items: flex-start;
  padding: 1em 1em 0 1em; }
  .Search .searchTitle {
    font-family: "Oswald", sans-serif;
    font-size: 2em;
    color: #fff; }
    .Search .searchTitle h2 {
      font-weight: 100;
      margin: 0;
      text-transform: uppercase;
      font-size: 1.2em;
      padding-bottom: 10px; }
  .Search .searchForm {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 30px; }
  .Search input {
    width: 70%;
    outline: none;
    height: 100%;
    border-radius: 5px;
    border: none;
    box-shadow: 0px 0px 3px 5px #00000024;
    padding-left: 10px; }
  .Search button {
    width: 30%;
    height: 100%;
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
    border: none;
    background: #2196F3;
    color: #fff;
    position: relative;
    right: 5px;
    cursor: pointer;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    font-family: "Roboto Condensed", sans-serif;
    font-size: 1em; }
    .Search button:hover {
      background: #000; }

.Loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
  flex-grow: 1;
  flex-direction: column; }
  .Loading .Loading-logo {
    -webkit-animation: Loading-logo-spin infinite 20s linear;
            animation: Loading-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
    color: #fff; }

@-webkit-keyframes Loading-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes Loading-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

* {
  box-sizing: border-box; }

html {
  background: radial-gradient(rgba(40, 44, 52, 0.95) 0%, rgba(40, 44, 52, 0.5) 90%), url(/googleBooks/static/media/booksBG.ac66c3e9.jpg);
  background-size: cover;
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%; }

#root {
  height: 100%; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

